      vm.api.searchByExample(filterOptions, sortOptions, vm.paginationOptions.perPage, vm.$refs.MDKTable.currentPage)
      .then(res => {
        vm.items = res.content;
        vm.paginationOptions.totalCount = res.totalElements;
      })
    },
